
import React, { useState } from 'react';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

const CreateProject = ({ onCreate }) => {
	const [fields, setFields] = useState({
		ProjectName: '',
		ProjectID: '',
		ProjectDescription: '',
		AuthorizedUsers: '',
	});

	const handleChange = (e) => {
		setFields({ ...fields, [e.target.name]: e.target.value });
	};

	const allFilled = Object.values(fields).every((val) => val.trim() !== '');

	const handleCreate = () => {
		if (onCreate) onCreate(fields);
		setFields({
			ProjectName: '',
			ProjectID: '',
			ProjectDescription: '',
			AuthorizedUsers: '',
		});
	};

	return (
		<div style={{ width: 550, maxHeight: 400, margin: '0 auto', padding: 24, border: '1px solid #ccc', borderRadius: 8 }}>
			<div style={{ marginBottom: 12 }}>
				<TextField
            label="Project Name"
            id="ProjectName"
            name="ProjectName"
            value={fields.ProjectName}
            onChange={handleChange}
            fullWidth
            margin="dense"
          />
			</div>
			<div style={{ marginBottom: 12 }}>
				<TextField
            label="ProjectID"
            id="ProjectID"
            name="ProjectID"
            value={fields.ProjectID}
            onChange={handleChange}
            fullWidth
            margin="dense"
          />
			</div>
			<div style={{ marginBottom: 12 }}>
				<TextField
            label="Project Description"
            id="ProjectDescription"
            name="ProjectDescription"
            value={fields.ProjectDescription}
            onChange={handleChange}
            fullWidth
            margin="dense"
          />
			</div>
			<div style={{ marginBottom: 12 }}>
				<TextField
            label="List of Authorized Users"
            id="AuthorizedUsers"
            name="AuthorizedUsers"
            value={fields.AuthorizedUsers}
            onChange={handleChange}
            fullWidth
            margin="dense"
          />
			</div>
			<div style={{ display: 'flex', justifyContent: 'center', width: '100%'}}>
				<Button
            variant="contained"
            color="primary"
            onClick={handleCreate}
            disabled={!allFilled}
            sx={{ fontWeight: 'bold', mb: 2 }}
          >
            Create Project
          </Button>
			</div>
		</div>
	);
};

export default CreateProject;
