
import React, { useState } from 'react';
import Project from './Project';
import CreateProject from './CreateProject';


const Projects = () => {
	const [projects, setProjects] = useState([]);

	const handleAddProject = (fields) => {
		setProjects((prev) => [...prev, fields]);
	};

	return (
		<div style={{ border: '1px solid #000', borderRadius: '16px', padding: '12px', maxWidth: 900, margin: '40px auto', background: '#fff' }}>
			<div style={{ fontWeight: 'bold', textAlign: 'center', fontSize: '2rem', margin: '24px 0' }}>
				Projects
			</div>
			<div style={{ display: 'flex', flexDirection: 'column', gap: '12px', alignItems: 'center' }}>
				{projects.map((project, idx) => (
					<Project key={idx} {...project} />
				))}
			</div>
			<div style={{ marginTop: '40px' }}>
				<CreateProject onCreate={handleAddProject} />
			</div>
		</div>
	);
};

export default Projects;
