import React from 'react';

const Snackbar = ({ open, message, onClose }) => {
  if (!open) return null;
  return (
    <div style={{
      position: 'fixed',
      bottom: 32,
      left: '50%',
      transform: 'translateX(-50%)',
      background: '#323232',
      color: '#fff',
      padding: '16px 32px',
      borderRadius: 8,
      boxShadow: '0 2px 8px rgba(0,0,0,0.2)',
      zIndex: 9999,
      fontSize: '1rem',
      minWidth: 200,
      textAlign: 'center',
      transition: 'opacity 0.3s',
    }}
      onClick={onClose}
    >
      {message}
    </div>
  );
};

export default Snackbar;
