
import React, { useState, useContext } from 'react';
import axios from 'axios';
import { SnackbarContext } from '../App';

const HardwareManagement = ({ label, projectId = 'P1' }) => {
    const capacity = 100;
    const [availability, setAvailability] = useState(100);
    const [quantity, setQuantity] = useState('');
    const { showSnackbar } = useContext(SnackbarContext);

    const handleQuantityChange = (e) => {
        setQuantity(e.target.value);
    };

    const handleCheckout = async () => {
        const qty = Number(quantity) || 0;
        if (qty > 0 && qty <= availability) {
            try {
                const res = await axios.post('http://localhost:5000/checkout_hardware', {
                    projectId,
                    qty
                });
                setAvailability(availability - qty);
                showSnackbar(res.data.message);
            } catch (err) {
                showSnackbar('Checkout failed');
            }
        }
        setQuantity('');
    };

    const handleCheckin = async () => {
        const qty = Number(quantity) || 0;
        if (qty > 0 && availability + qty <= capacity) {
            try {
                const res = await axios.post('http://localhost:5000/checkin_hardware', {
                    projectId,
                    qty
                });
                setAvailability(availability + qty);
                showSnackbar(res.data.message);
            } catch (err) {
                showSnackbar('Check-in failed');
            }
        }
        setQuantity('');
    };

    return (
        <div>
            <div style={{ display: 'flex', gap: '12px', alignItems: 'center', marginTop: 4, marginBottom: 4 }}>
                <div style={{ minWidth: 160 }}>{label}: {availability}/{capacity}</div>
                <div>
                    <input
                        type="number"
                        id="quantity"
                        name="quantity"
                        placeholder="Enter Quantity"
                        min="0"
                        value={quantity}
                        onChange={handleQuantityChange}
                        style={{ width: 100 }}
                    />
                </div>
                <button onClick={handleCheckout}>Check-out</button>
                <button onClick={handleCheckin}>Check-in</button>
            </div>
        </div>
    );
};

export default HardwareManagement;
