
import React, { useState, useContext } from 'react';
import axios from 'axios';
import ProjectDetails from './ProjectDetails';
import HardwareManagement from './HardwareManagement';
import { SnackbarContext } from '../App';

const Project = ({ ProjectName, ProjectID, ProjectDescription, AuthorizedUsers }) => {
	const [joined, setJoined] = useState(false);
	const [bgColor, setBgColor] = useState('#f5f5f5');
	const { showSnackbar } = useContext(SnackbarContext);
	const projectId = ProjectID || 'P1';

	const handleAccessClick = async () => {
		if (!joined) {
			try {
				const res = await axios.post('http://localhost:5000/join_project', { projectId });
				showSnackbar(res.data.message);
				setBgColor('#e3f2fd');
			} catch (err) {
				showSnackbar('Join failed');
			}
		} else {
			try {
				const res = await axios.post('http://localhost:5000/leave_project', { projectId });
				showSnackbar(res.data.message);
				setBgColor('#f5f5f5');
			} catch (err) {
				showSnackbar('Leave failed');
			}
		}
		setJoined((prev) => !prev);
	};

	return (
		<div style={{ backgroundColor: bgColor, width: '560px', padding: '20px', borderRadius: '12px' }}>
			<div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
				<div style={{ flex: 1 }}>
					<ProjectDetails 
						ProjectName={ProjectName}
						ProjectID={ProjectID}
						ProjectDescription={ProjectDescription}
						AuthorizedUsers={AuthorizedUsers}
					/>
					<HardwareManagement label="HWSet1" projectId={projectId} />
					<HardwareManagement label="HWSet2" projectId={projectId} />
				</div>
				<button
					onClick={handleAccessClick}
					style={{
						backgroundColor: joined ? '#e53935' : '#1976d2',
						color: '#fff',
						fontWeight: 'bold',
						border: 'none',
						padding: '10px 20px',
						borderRadius: '5px',
						cursor: 'pointer',
						marginLeft: '32px',
						alignSelf: 'center',
					}}
				>
					{joined ? 'Leave' : 'Join'}
				</button>
			</div>
		</div>
	);
};

export default Project;
